* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  font-size: 18px;
  box-sizing: border-box;
  font-family: "Epilogue", sans-serif;
}

body {
  font-size: 0.8rem;
  max-width: 2500px;
  min-height: 100vh;
  background-color: #ffffff;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
li {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol,
ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

.ftr {
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: auto;
  background-color: #d5966c;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

#map {
  min-height: 100%;
}

.grid {
  display: grid;
  grid-template-rows: 2fr 2fr 1fr;
  min-height: 100vh;
}

.location-info {
  color: #ffffff;
  background-color: #151515;
}
.location-info .location-info__header {
  font-size: 50px;
  line-height: 88px;
  color: currentColor;
}
.location-info .location-info__header + .location-info__sub-heading {
  color: #d5966c;
}